<template>
<div id="header">
  <div>
    <el-avatar :size="avatar_size" :src="imgUrl"></el-avatar>
  </div>

  <div class="motto">
    <h2>
      对自己和陌生人谦虚,对朋友骄傲
    </h2>
  </div>

  <div class="date">
    <p>当前日期:<el-input
      v-model="date"  :disabled="true"
       style="color: red"/>
    </p>
  </div>
</div>
</template>

<script>
    export default {
        name: "Header",
        data:function () {
          return{
            avatar_size:130,
            imgUrl: require("../assets/portrait.jpg"),
            date:'',
            timer:null
          }
        },
      created() {
        var _this = this; //声明一个变量指向Vue实例this，保证作用域一致(异步操作)
        //初始化时间
        var current = new Date();
        var minute = current.getMinutes();
        //排除分钟前面无0的情况
        if( current.getMinutes()<10){
          minute='0'+current.getMinutes();
        }
        _this.date = //修改数据date
          current.getFullYear() +
          "-" +
          (current.getMonth() + 1) +
          "-" +
          current.getDate() +
          " " +
          current.getHours() +
          ":" +
          current.getMinutes();

        //设置定时器
        this.timer = setInterval(function() {
          var current = new Date();
          _this.date = //修改数据date
            current.getFullYear() +
            "-" +
            (current.getMonth() + 1) +
            "-" +
            current.getDate() +
            " " +
            current.getHours() +
            ":" +
            minute
        }, 1000);
      },
      beforeDestroy() {
        if (this.timer) {
          clearInterval(this.timer); // 在Vue实例销毁前，清除我们的定时器
        }
      }
    }
</script>

<style scoped>
  #header{
    display: flex;
    height: 10rem;
    justify-content: space-around;
    align-items: center;

  }

  h2{
    font-family: 华文隶书;
  }


  .motto, .date{
    height: 90%;
    display: flex;
    align-items: flex-end;
    color: white;
  }
</style>



